.home {
  padding: 4vw;
  margin: 3vw auto;
  border-radius: 15px;
  background: linear-gradient(
    140deg,
    #9fffe4,
    #ffa8a8
  ); /* White Background for Contrast */
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  max-width: 1000px;
}

/* Profile Image Animation */
.profile-container {
  text-align: center;
  margin-bottom: 20px;
}

.profile-pic {
  width: 200px;
  height: 200px;
  border-radius: 60%;
  object-fit: cover;
  border: 3px solid #40c9a2;
  opacity: 0;
  transform: translateY(-100px);
}
img:hover {
  zoom: 1.7;
}

@media (max-width: 768px) {
  .profile-pic {
    width: 150px;
    height: 150px;
  }
  img:hover {
    zoom: 1.3;
  }
}

.animate-profile {
  animation: slideIn 1s forwards ease-in-out;
}

@keyframes slideIn {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Text Popup Animation */
.intro-text {
  text-align: center;
  opacity: 0;
  transform: scale(2);
}

.animate-text {
  animation: popup 1s forwards ease-out;
}

@keyframes popup {
  to {
    opacity: 1;
    transform: scale(1);
  }
}
@media (max-width: 768px) {
  @keyframes popup {
    to {
      opacity: 1;
      transform: scale(0.7);
    }
  }
}

/* Tagline Animation - Letters Collecting from All Sides */
.tagline {
  text-align: right; /* Align text to the right */
  /* Adjust spacing from the right edge */
  /* Ensure a consistent font size */
  color: #4e4e4e; /* Optional: Text color */
  line-height: 1.5; /* Adjust line spacing for readability */
  font-family: "Montserrat", sans-serif; /* Maintain font consistency */
}

.animate-tagline {
  animation: textAssemble 5s forwards ease-in-out;
}

@keyframes textAssemble {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.tagline strong {
  display: inline-block;
  text-align: center;
  line-height: 1.5; /* Improves readability for multi-line text */
}

.tagline strong span {
  display: inline-block;
  position: relative;
  animation: flyIn 1s ease-out;
}

@keyframes flyIn {
  0% {
    opacity: 0;
    transform: translate(0, -100px);
  }
  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}

/* For Mobile Screens */
@media (max-width: 768px) {
  .tagline {
    font-size: 20px; /* Adjust font size for smaller screens */
    text-align: center;
  }

  .tagline strong span {
    animation: flyIn 1s ease-out; /* Keep animation same for consistency */
  }
}

/* For Desktop Screens */
@media (min-width: 769px) {
  .tagline {
    font-size: 30px; /* Larger font size for desktops */
    text-align: center;
  }
}
