/* About Section Styling */
.about {
  padding: 50px 20px;
  text-align: center;
  background: linear-gradient(140deg, #88e3fff6, #fff5a8de);
  color: #4e4e4e;
  font-family: "Poppins", sans-serif;
}

.about-heading h2 {
  color: #40c9a2;
}

/* Education Section Styling */
.education-section {
  display: flex;
  align-items: center;
  margin: 30px 0;
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.8s ease-in-out;
  padding: 20px;
  font-family: "Poppins", sans-serif;
  color: #4e4e4e;
}

/* Default styles for PC (desktop and larger screens) */
.education-section img.education-image {
  width: 300px;
  height: 300px;
  border-radius: 10px;
  margin-right: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

/* Styles for phone screens (max-width 768px is a common breakpoint) */
@media (max-width: 768px) {
  .education-section img.education-image {
    width: 120px; /* Smaller width */
    height: 120px; /* Smaller height */
    margin-right: 10px; /* Adjust margin */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); /* Softer shadow */
  }
}

img.education-image {
  zoom: 0;
}

.education-section p.education-text {
  font-size: 1.2rem;
  text-align: left;
  font-family: "Poppins", sans-serif;
}
/* styling for phone*/
@media (max-width: 768px) {
  .education-section p.education-text {
    font-size: 0.8rem;
    text-align: left;
    font-family: "Poppins", sans-serif;
  }
}

/* Zig-Zag Layout */
.animated-left {
  flex-direction: row;
  animation: slideInLeft 0.8s ease-in-out forwards;
}

.animated-right {
  flex-direction: row-reverse;
  animation: slideInRight 0.8s ease-in-out forwards;
}

/* Keyframes for the Zig-Zag Animation */
@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Delayed Animation */
.education-section:nth-child(2) {
  animation-delay: 1s;
}

.education-section:nth-child(3) {
  animation-delay: 2s;
}

.education-section:nth-child(4) {
  animation-delay: 3s;
}

/* About Heading Styling */
.about-heading {
  font-size: 2.5rem;
  margin-bottom: 30px;
  text-transform: uppercase;
  color: #4e4e4e;
  border-bottom: 3px solid #40c9a2;
  padding-bottom: 10px;
  margin-bottom: 20px;
}
@media (max-width: 768px) {
  /* About Heading Styling */
  .about-heading {
    font-size: 2rem;
    margin-bottom: 30px;
    text-transform: uppercase;
    color: #4e4e4e;
    border-bottom: 3px solid #40c9a2;
    padding-bottom: 10px;
    margin-bottom: 20px;
  }
}
/* Group for Each Institution */
.certification-group {
  display: flex;
  align-items: flex-start; /* Align items to the top */
  justify-content: flex-start; /* Align items to the left */
  margin-bottom: 50px;
  position: relative;
  padding: 20px;
}

/* Institution Logo */
.institution-logo {
  width: 200px; /* Corrected typo from '200x' to '200px' */
  height: auto;
  padding-top: 10px;
  margin-right: 50px;
  margin-left: 200px;
  animation: floatLogo 5s infinite ease-in-out;
}
@media (max-width: 768px) {
  .institution-logo {
    width: 100px; /* Corrected typo from '200x' to '200px' */
    height: auto;
    padding-top: 10px;
    margin-right: 40px;
    margin-left: auto;
    animation: floatLogo 5s infinite ease-in-out;
  }
}
.institution-logo.forage {
  height: auto;
}
/* Floating Animation for Logos */
@keyframes floatLogo {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-15px);
  }
  100% {
    transform: translateY(0);
  }
}

/* Certificate List Styling */
.certificates {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-size: 1.2rem;
  max-width: 400px;
}

@media (max-width: 768px) {
  .certificates {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-size: 0.9rem;
    max-width: 400px;
  }
}

.certificate-list {
  list-style-type: none;
  padding-left: 20px;
  margin-top: 0;
}

.certificate-list li {
  margin: 10px 0;
  position: relative;
  display: flex;
  align-items: center; /* Align the text and arrow horizontally */
}

/* Connect the Certificates in a Tree Layout */
.certificate-list li:before {
  content: "â†’";
  position: absolute;
  left: -25px;
  top: 50%;
  transform: translateY(-50%);
  color: #40c9a2;
  font-size: 1.5rem;
  line-height: 1; /* Ensure the arrow is aligned with the certificate text */
}

/* Heading for Institutions */
.certificates h4 {
  font-size: 1.8rem;
  margin-bottom: 20px;
  color: #4e4e4e;
  text-align: left; /* Align the heading left for consistency */
  margin-top: 0; /* Ensure consistent top margin */
}

/* Specific styling for LetsUpgrade and Pieces for Developers */
.letsupgrade .institution-logo {
  animation-delay: 1s;
}
.pieces-for-dev .institution-logo {
  animation-delay: 3s;
}
.forage .institution-logo {
  animation-delay: 5s; /* Remove delay for consistent animation start */
}

.letsupgrade h4,
.pieces-for-dev h4,
.forage h4 {
  margin-top: 0; /* Fix top margin for all headings */
  text-align: left;
  font-size: 1.8rem; /* Maintain consistency in font size */
}

@media (max-width: 768px) {
  .letsupgrade h4,
  .pieces-for-dev h4,
  .forage h4 {
    margin-top: 0; /* Fix top margin for all headings */
    text-align: left;
    font-size: 1.2rem; /* Maintain consistency in font size */
  }
}

.institution-logo:hover {
  zoom: 0; /* Ensures no zoom effect occurs */
}

/* Skills Section */
.skills-section {
  text-align: center;
  margin-top: 50px;
}

.skills-section h3 {
  font-size: 2.5rem;
  color: #4e4e4e;
  margin-bottom: 20px;
  border-bottom: 3px solid #40c9a2;
  padding-bottom: 10px;
}

.skills-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px; /* Space between bubbles */
  position: relative;
  max-width: 600px; /* Ensure only 3 items fit per line */
  margin: 0 auto; /* Center the container */
}
@media (max-width: 100px) {
  .skills-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 0px; /* Space between bubbles */
    position: relative;
    max-width: 10px; /* Ensure only 3 items fit per line */
    margin: 0 auto; /* Center the container */
  }
}

/* Bubble Styling */
.skill-item {
  width: 180px;
  height: 180px;

  /* Circular shape */
  display: flex;
  justify-content: center;
  align-items: center;

  position: relative;
  animation: floatBubble 3s ease-in-out infinite; /* Floating animation */
  transition: transform 0.2s ease-in-out;
}

@media (max-width: 768px) {
  .skill-item {
    width: 70px;
    height: 70px;
  }
}

/* Floating Animation */
@keyframes floatBubble {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-15px);
  }
}

.skill-image {
  width: 60%; /* Adjust logo size */
  height: auto;
  object-fit: contain;
}

@media (max-width: 768px) {
  .skill-image {
    width: 60%; /* Adjust logo size */
    height: auto;
    object-fit: cover;
  }
}
