/* Experience Page Styles */

/* Container Styling */
.experience {
  padding: 4vw;
  margin: 5vw auto;
  border-radius: 15px;
  /* White background for contrast */
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
  max-width: 1100px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  background: linear-gradient(150deg, #baf5fd, #ffe8c3);
}

/* Heading */
.experience h2 {
  color: #4e4e4e;
  text-transform: uppercase;
  font-size: 2.5rem;
  text-align: center;
  border-bottom: 4px 100 #40c9a2; /* Mint Green */
  padding-bottom: 10px;
  margin-bottom: 30px;
}

/* Section Styling */
.experience-section {
  display: flex;
  align-items: flex-start;
  margin-bottom: 25px;
  padding: 15px;
  border-style: none;
  border-radius: 50px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  background-color: rgb(219, 248, 245);
}

.experience-section:hover {
  transform: translateX(10px);
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.15);
}

.experience-icon {
  color: #40c9a2;
  font-size: 2.5rem;
  margin-right: 20px;
}

.experience-section h3 {
  color: #4e4e4e;
  font-size: 1.8rem;
  margin-bottom: 10px;
}

.experience-section p,
.experience-section ul {
  color: #4e4e4e;
  font-size: 1rem;
  line-height: 1.6;
  margin: 5px 0;
}

.experience-section ul {
  list-style: disc;
  padding-left: 20px;
}

.experience-section ul li {
  margin-bottom: 5px;
}

/* Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.experience {
  animation: fadeIn 0.8s ease-out;
}

/* Responsive Design */
@media (max-width: 768px) {
  .experience-section {
    flex-direction: column;
    align-items: flex-start;
  }

  .experience-icon {
    margin-bottom: 10px;
  }

  .experience-section h3 {
    font-size: 1.6rem;
  }

  .experience-section p,
  .experience-section ul {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .experience h2 {
    font-size: 1.8rem;
  }

  .experience-section h3 {
    font-size: 1.4rem;
  }

  .experience-section p,
  .experience-section ul {
    font-size: 0.8rem;
  }
}
