/* Global Styles */
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  background: linear-gradient(
    120deg,
    #ffefd5,
    #e0f7fa
  ); /* Light Peach and Sky Blue */
  color: #4e4e4e; /* Soft Charcoal Gray */
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Poppins", sans-serif;
  color: #4e4e4e;
  margin: 0;
}

a {
  color: #ff6f61; /* Pastel Pink */
  text-decoration: none;
  transition: color 0.3s ease;
}

a:hover {
  color: #40c9a2; /* Mint Green */
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  position: relative;
  z-index: 1;
}

.navbar h1 {
  font-size: 1.8rem;
  color: #19b589; /* White Text */
  flex-grow: 1; /* Ensure navbar title takes space */
}

.navbar ul {
  list-style: none;
  display: flex;
  gap: 15px;
  margin: 0;
  padding: 0;
}

.navbar ul li a {
  color: #4e4e4e; /* White Text */
  font-size: 1.1rem;
}

.navbar ul li a:hover {
  color: #40c9a2; /* Golden Yellow */
}

/* Remove or adjust the media query for mobile devices */
@media (max-width: 768px) {
  .navbar {
    /* Keep the same styles as the PC version */
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    position: relative;
    z-index: 1;
  }

  .navbar h1 {
    font-size: 1.8rem; /* Keep the same font size as PC */
  }
}

/* Section Styling */
.about,
.projects,
.experience,
.contact,
.home {
  padding: 4vw;
  margin: 5vw auto;
  border-radius: 15px;
  background: #ffffff; /* White Background for Contrast */
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  max-width: 1100px;
}

h2 {
  color: #4e4e4e;
  text-transform: uppercase;
  border-bottom: 3px solid #40c9a2; /* Mint Green */
  padding-bottom: 10px;
  margin-bottom: 20px;
  font-size: 2rem;
  text-align: center;
}

/* Buttons */
button,
.btn {
  background: linear-gradient(90deg, #40c9a2, #ff6f61); /* Gradient Button */
  color: #ffffff; /* White Text */
  border: none;
  padding: 12px 24px;
  border-radius: 25px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

/* Inputs */
input,
textarea {
  width: 100%;
  padding: 12px;
  margin: 15px 0;
  border: 1px solid #dee2e6;
  border-radius: 10px;
  background: #fafafa;
  color: #4e4e4e;
  font-size: 1rem;
}

input:focus,
textarea:focus {
  outline: none;
  border-color: #40c9a2; /* Mint Green */
  box-shadow: 0 0 8px rgba(64, 201, 162, 0.5);
}

/* Footer */
footer {
  text-align: center;
  padding: 20px;
  background: #f8f9fa;
  color: #4e4e4e;
  font-size: 0.9rem;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .navbar {
    flex-direction: column;
    align-items: center;
    padding: 15px 20px;
  }

  .navbar ul {
    flex-direction: column;
    gap: 10px;
    align-items: center;
  }

  .navbar h1 {
    font-size: 1.5rem;
  }

  h2 {
    font-size: 1.8rem;
  }

  button,
  .btn {
    padding: 10px 20px;
    font-size: 0.9rem;
  }
}

@media (max-width: 768px) {
  .about,
  .projects,
  .experience,
  .contact,
  .home {
    padding: 5vw;
    margin: 5vw auto;
  }

  h2 {
    font-size: 1.6rem;
  }

  button,
  .btn {
    font-size: 0.85rem;
  }
}

@media (max-width: 480px) {
  .navbar ul {
    gap: 5px;
  }

  .about,
  .projects,
  .experience,
  .contact,
  .home {
    padding: 3vw;
    margin: 3vw auto;
  }

  h2 {
    font-size: 1.4rem;
  }

  button,
  .btn {
    padding: 8px 16px;
    font-size: 0.8rem;
  }
}
