.contact {
  text-align: center;
  font-family: "Poppins", sans-serif;
}

.contact h2 {
  margin-bottom: 20px;
}

.contact p {
  font-size: 16px;
  color: #555;
  margin-bottom: 20px;
}

.contact-list {
  list-style: none;
  padding: 0;
}

.contact-list li {
  margin: 10px 0;
}

.contact-list a {
  color: #049b71;
  font-size: 18px;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  transition: color 0.3s ease;
}

.contact-list a:hover {
  color: #157256;
}

.contact-list svg {
  font-size: 24px;
}

.contact {
  background: linear-gradient(145deg, #dcffa4de, #d1b1fff6);
}
